ess card locating and tracking with Vuforia engine, it also handles the 3D AR avatar model rendering, with more than 10 custom models for users to select. The unity app is integrated within the flutter UI, both communicates by serial messaging signals. Model is programmatically loaded, as we need to handle different choice of avatar from different requests. Each model has two separate animator controller, which uses script to randomly generate animations. For example, around 8 different idle animations (rub shoulder etc) will be played when the avatar is idle, to make them more realistic. While talking, different talking gestures will also be randomly played, mouth animation will also be played, which imitates speak, however, not all of the avatars is able to play the mouth animation, as they have different bone rigs, this should be improved in the future. "),_c('br'),_c('br'),_c('carousel',{staticStyle:{"text-align":"center"},attrs:{"perPage":1,"navigationEnabled":true}},_vm._l((5),function(n){return _c('slide',{key:n},[_c('img',{staticClass:"img-responsive slideImg",attrs:{"src":'images/design/implementation/unity'+n+'.png'}})])}),1),_c('br'),_vm._v(" We have also added head look controller, as the avatar can be viewed from any angle in AR (the user can tilt or rotate the business card as they like), the avatar's head will always looking straight, which is unnatural. With the head look controller, the avatar will look at the camera like he/she is talking to the user in real life. We have limited the angle of the head, thus the avatar won't have excessive head motion (e.g. turning all the way to back). However, when playing animations, the head rotation might be imposed by the animation, causing a little bit of unnatural movements. This should be improved in the future. "),_c('br'),_c('br'),_vm._v(" Apart form the AR scene, we have later added a non-AR scene, in case user do not wish to hold their camera to be able to talk or interact with the avatar. The scene use